import React from 'react'
import { Element } from 'react-scroll'
import { StaticImage } from "gatsby-plugin-image"

import Box from '../../../components/Box'
import ChartPageModule from '../../../components/ChartPageModule'

const charts = [
  {
    buttonText: '投資高效率',
    title: '從各類資產的夏普值來評估投資效率，新興市場企業債券可謂首選',
    image_molibe: <StaticImage layout="constrained" src="3-1.png" alt="3-1" />,
    image_desk: <StaticImage layout="constrained" src="3-1-desk.png" alt="3-1-desk" />,
    source: 'Bloomberg，凱基投信整理。美國股市為S&P500指數、新興市場股市為MSCI新興市場股票指數、全球股市為MSCI世界指數，美國公債、美國投資級債、美國高收益債為彭博巴克萊債券指數，新興市場主權債、新興市場企業債為JP摩根債券指數。資料日期：2009/1/1~2021/6/30'
  },
  {
    buttonText: '新興更有利',
    title: '在投資級債及非投資級債的個別領域中，新興市場企業債為取得更佳收益的選項',
    image_molibe: <StaticImage layout="constrained" src="3-2.png" alt="3-2" />,
    image_desk: <StaticImage layout="constrained" src="3-2-desk.png" alt="3-2-desk" />,
    source: 'Bloomberg，收益率為最低殖利率(Yield to worst)，新興市場高收益企業債及新興市場投資級企業債均為JP摩根債券指數、其餘債券均為彭博巴克萊指數。資料日期：2021/6/30'
  },
]

const BgOrange = () => <Box.FullAbs><StaticImage src="orange_bg.png" alt="bgblock" /></Box.FullAbs>

const FocusTwo = ({ page }) => {
  const show = page === 'focusTwo'
  return (
    <Element name="focusTwo">
      <ChartPageModule
        charts={charts}
        background="custom.lightYellow"
        mobileCircle={<StaticImage src="orange.png" alt="orange" />}
        deskCircle={<StaticImage src="orange_desk.png" alt="orange_desk" />}
        title="焦點2"
        text="新興市場"
        prevArrow={<StaticImage src="arrow_orange.png" alt="arrow" />}
        nextArrow={<Box transform="rotate(180deg) translateY(50%)"><StaticImage src="arrow_orange.png" alt="arrow" /></Box>}
        activeBg={<BgOrange />}
        sourceBorderColor="custom.darkOrange"
        show={show}
      />
    </Element>
  )
}

export default FocusTwo
